// popups
function showPopup(code) {
    console.log('show popup code: ' + code);
    $.fancybox.open({
        src: '#popup-' + code,
        type: 'inline',
        padding: 0,
        afterShow: function () {
            //$("a.fancybox-close").addClass('has-custom');

            maskPhone();
            //$('input[name="form_description"]').val(description);
        }
    });
}

function showAjaxPopup(url, params) {
    var href = url;
    if (params) {
        var query = $.param(params)
        href += '?' + query;
    }


    $.fancybox.open({
        src: href,
        type: 'ajax',
        padding: 0,
        afterShow: function () {
            maskPhone();
            initForm($('form.ajax-popup-form.no-init').removeClass('no-init'));
        }
    });
}

function closePopup() {
    $.fancybox.close();
}


// popups done

//base actions
function initBaseActions() {
    $('[data-showpopup]').on('click', function (e) {
        e.preventDefault();
        showPopup($(this).data('showpopup'));
    });

    /*$('[data-href]').on('click', function (e) {
        e.preventDefault();
        window.location.href = $(this).data('href');
    });*/
}

//base actions done

function maskPhone() {
    $('input[name="phone"]').inputmask('+7 (999) 999 - 99 - 99');
}


